import React, { Component } from 'react';
import TodoItems from './TodoItems';
import './TodoList.css';
class TodoList extends React.Component {
  constructor(props, context) {
    super(props, context);
 
    this.state = { //defining an items array/property that will be responsible for storing all of the various items that can be entered.
      items: []
    };
    
    this.addItem = this.addItem.bind(this); //ensure the this keyword resolves properly
    this.deleteItem = this.deleteItem.bind(this);//This will ensure that all references to this inside deleteItem will reference the correct thing.
  }
  
  addItem(e) { //define our addItem event handler 
    var itemArray = this.state.items;
   
    if (this._inputElement.value !== "") {
      itemArray.unshift(
        {
          /* We are actually adding an object that contains both the entered text as well as a unique key value set by the current time (Date.now()) */
          text: this._inputElement.value,
          key: Date.now()
        }
      );
   
      this.setState({ //We are setting our state's items property to the value of itemArray
        items: itemArray
      });
   
      this._inputElement.value = ""; //We are clearing the value of our input element to make room for the next todo item. 
    }
   
    console.log(itemArray);
     
    e.preventDefault(); //We are overriding this event's default behavior.
  }
  
  /* We are passing the key from our clicked item all the way here, and we check this key against all of the items we are storing currently via the filter method:
   * We create a new array called filteredItems that contains everything except the item we are removing. This filtered array is then set as our new items property on our state object: */
  deleteItem(key) {
    var filteredItems = this.state.items.filter(function (item) {
      return (item.key !== key);
    });
   
    this.setState({
      items: filteredItems
    });
  }

  render() {
    return (
      <div className="todoListMain">
        <div className="header">
          <form onSubmit={this.addItem}> {/* We listen for the submit event on the form itself, and we call the addItem method when that event is overheard */}
            {/* storing a reference to our input element in the appropriately named _inputElement property. */}
            <input ref={(a) => this._inputElement = a}
                   placeholder="enter task">
            </input>
            <button type="submit">add</button>
          </form>
        </div>
        <TodoItems entries={this.state.items}
                   delete={this.deleteItem}/> {/* This change ensures our TodoItems component now has knowledge of a prop called delete */ }
      </div>
    );
  }
};
 
export default TodoList;


