import React, { Component } from 'react';
import FlipMove from 'react-flip-move';
 
class TodoItems extends React.Component {
  constructor(props, context) {
    super(props, context);
 
    this.createTasks = this.createTasks.bind(this);
    this.delete = this.delete.bind(this);//define our delete event handler
  }
 
  createTasks(item) {
    return <li onClick={(e) => this.delete(item.key, e)} /* set up the event handler for dealing with the click event. All we are doing is listening to the click event and associating it with an event handler called delete */
                key={item.key}>{item.text}</li>
  }
  
  delete(key) {
    this.props.delete(key);
  }
 
  //we are taking the list of todo items (passed in as entries) and turning them into JSX/HTML-ish elements
  render() {
    var todoEntries = this.props.entries;
    var listItems = todoEntries.map(this.createTasks);
 
    return (//turn this list of element into something we can show on screen with the following:
      <ul className="theList">
        <FlipMove duration={1000} easing="ease-out">
          {listItems}
        </FlipMove>
      </ul>
    );
  }
};
 
export default TodoItems;


